input {
    display: block;
    width: 100%;
}

.form {

    & input:invalid {
        box-shadow: none;
    }

    & input[type="text"],
    & input[type="date"],
    & input[type="number"],
    & input[type="password"],
    & input[type="email"],
    & input[type="tel"] {
        height: 52px;
        font-size: 14px;
        padding-left: rem(15);
        padding-right: rem(15);
        border: 1px solid #BCBCBC;
        box-sizing: border-box;
        border-radius: 5px;

        &::placeholder {
            color: $main;
        }
    }

    & input[type="date"] {
        padding-right: 3em;
        background-image: url(data:image/svg+xml;base64,);
        background-size: 1.2em auto;
        background-repeat: no-repeat;
        background-position: calc(100% - 1.25em) center;
        cursor: pointer;
    }

    & input[type="date"][aria-valid="false"],
    & input[type="text"][aria-valid="false"],
    & input[type="number"][aria-valid="false"],
    & input[type="tel"][aria-valid="false"],
    & input[type="password"][aria-valid="false"],
    & input[type="email"][aria-valid="false"] {
        border-color: #f00;
    }

    & .af-select[aria-valid="false"] {
        border-color: #f00;
    }

    & input[type="password"][aria-valid="true"],
    & input[type="email"][aria-valid="true"] {
        //border-color: #0f0;
        padding-right: 3em;
        background-image: url(data:image/svg+xml;base64,);
        background-size: 1em auto;
        background-repeat: no-repeat;
        background-position: 96% center;
    }

    & textarea {
        min-height: 120px;
        max-width: 100%;
        resize: vertical;
        width: 100%;
        border: 1px solid #BCBCBC;
        border-radius: 5px;
        font-size: 14px;
        padding: rem(15);

        &:focus {
            outline: none !important;
        }
    }

    &__note {
        text-decoration: underline;
        font-size: 13px;
        color: $main;
        width: 100%;

        & span {
            cursor: pointer;
        }

        &--col2 {
            display: flex;
            justify-content: space-between;
            flex-direction: column;

            &>span {
                cursor: pointer;
                padding-bottom: 10px;

                @media screen and (min-width: $w760) {
                    padding-bottom: 0;
                }
            }

            @media screen and (min-width: $w760) {
                flex-direction: row;
            }
        }
    }

    &__submit {
        padding-top: 3em;
    }

    // &__item {
    //     // display: flex;
    //     // align-items: center;
    //     // margin: -10px;
    // }

    // &__subitem {
    //     // flex-grow: 1;
    //     // width: 100%;
    //     // padding: 12px 10px;
    // }

    &__item {
        display: flex;
        align-items: center;
        flex-direction: column;
        width: 100%;

        @media screen and (min-width: $w760) {
            flex-direction: row;
            margin: -10px;
            width: calc(100% + 20px);
        }

        &>.form__item {
            margin: 0;
            flex-direction: column;
            width: 100%;

            @media screen and (min-width: $w1440) {
                flex-direction: row;
            }
        }
    }

    &__subitem {
        flex-grow: 1;
        margin-bottom: 15px;
        margin-right: 0;
        width: 100%;
        display: flex;


        & .form__item {
            & .form__subitem {
                &:last-child {
                    margin-bottom: 0;
                }
            }
        }

        @media screen and (min-width: $w760) {
            padding: 12px 10px;
            margin-bottom: 0;
        }
    }

    &__label {

        font-weight: 600;
        font-size: 14px;
        text-transform: uppercase;
        color: $main;
        padding-top: 25px;
        padding-bottom: 15px;
    }

    & fieldset {
        border: none;
        padding: 0;
        margin-top: rem(40);


        &:first-child {
            margin-top: rem(0);
        }
    }

    & legend {
        margin-bottom: rem(30);
    }

    & .af-select {
        height: 52px;
        border: 1px solid #BCBCBC;
        box-sizing: border-box;
        border-radius: 5px;

        & .select-options li {
            max-width: none;
        }
    }



}

.remove-repeeatter {
    min-width: 52px;
    width: 52px;
    height: 52px;
    display: inline-flex;
    border: 1px solid #bcbcbc;
    border-radius: 5px;
    align-items: center;
    justify-content: center;
    position: relative;
    margin-left: 10px;
    cursor: pointer;

    &:hover {
        background-color: #eee;
    }

    &::before {
        content: '';
        width: 40%;
        display: inline-block;
        height: 1px;
        background-color: $main;
    }
}